/* eslint-disable no-unused-vars, no-param-reassign */

import { createSlice } from "@reduxjs/toolkit";

import { Group } from ".prisma/client";

export type GroupState = {
  activeGroup: Group | undefined,
};

export type GroupStateReducers = {
  setActiveGroup: (state: GroupState, action: {
    type: string,
    payload: { group: Group; },
  }) => void;
};

export const groupSlice = createSlice<GroupState, GroupStateReducers>({
  name: "group",
  initialState: {
    activeGroup: undefined,
  },
  reducers: {
    setActiveGroup: (state, action: {
      type: string,
      payload: { group: Group; },
    }) => {
      state.activeGroup = action.payload.group;
    },
  },
});

export const { setActiveGroup } = groupSlice.actions;
export const selectActiveGroup = (state: { activeGroup: GroupState; }) => state.activeGroup.activeGroup;

export default groupSlice.reducer;
